.Header{
    height:65px;
    width:100vw;
    justify-content: space-between;
    background-color: white;
    margin: 0;
    padding: 0;
    display: flex; 
    align-content: center;
    align-items: center;
    box-sizing: border-box;
    
   
    -webkit-box-shadow: 0 3px  rgba(57, 63, 72, 0.3);
    -moz-box-shadow: 0 3px rgba(57, 63, 72, 0.3);
    box-shadow: 0 3px  rgba(57, 63, 72, 0.3);

    @media(max-width:1200px){
      box-shadow:0 1px rgba(57, 63, 72, 0.3);
      width:100%;
      height: 100%;
    }
   
   
}

