.Main{
    margin:0;
    background-image: url('../../../images/main-background.png');
    background-size: cover;
    height:100%;
    display: flex;
    justify-content: space-between;
    background-position:center;
    width:100vw;
    padding: 0;
    box-sizing: border-box;
}


.LeftSide{
    display: flex;
    flex-direction: column;
    height: 100vh;
    width: 50vw;
    justify-content: flex-start;
    justify-items: center;
    padding:0;
    margin:0;
    box-sizing: border-box;
    
    @media (max-width:1200px){
       
        
        
    }
}


.WhatsappLogo{
    margin:0 ;
    padding: 0;
    @media (max-width:1200px){
    }
    img{
        height: 2rem;
        width: 2rem;

        &:hover{
            cursor: pointer;
        }
    }
}

.Texts{
    display: flex;
    flex-direction: column;
    margin:  0;

    font-size: 14px;
    @media (max-width:1200px){
        padding:0;
    }
    h2{
        color:white;
        padding: 1px ;
        margin:4rem 0 0 0 ;
        
    }
    h3{
        color:white;
        padding: 1px ;
        margin: 0;
        text-decoration: underline;
        text-transform: uppercase;
    }
    p{
        justify-items: center;
        justify-content: flex-start;
        color:white;
        padding:0.8rem 0 0 0 ;
        margin: 0;
        letter-spacing: 1px;
        white-space:1px;
        @media (max-width:1200px){
            width:70%;
        }
    }
   
}

.Icons{
    display: flex;
   
  padding: 0;
    margin: auto 0 0 0;
    @media(max-width:1200px){

    }
    p{  
       writing-mode: vertical-lr;
       text-orientation: sideways;
       padding:0;
       color:white;
       font-size:100%;
       margin:6px;
       @media (max-width:1200px){
        font-size:14px;
        padding:auto;
        margin-bottom:0 ;
        margin-left:5px;
        
    }
      
    }

    img{
        margin:auto 0 10px 0;
        padding: 0;
        height:80%;
       
        @media (max-width:1200px){
        
        }
    }
   
    
}

.RightSide{
    display: flex;
    flex-direction: column;
    height: 100vh;
    width: 50vw;
    justify-content: flex-start;
    justify-items: center;
    padding:0;
    margin:0;
    box-sizing: border-box;
   
    @media (max-width:1200px){
       margin:1rem 0 0 2rem;
    }

    ul{
        list-style: none;
        display:flex;
        margin:0.2rem 3rem 0 0;
        justify-content: flex-end;
        box-sizing: border-box;
        img{
            justify-content: space-around;
        }
        li{
            &:hover{
                cursor: pointer;
            }
        }

        
     
    }
    
    .Aimage{
        display: flex;
        padding:1rem;
        justify-content: flex-start;
        box-sizing: border-box;

        @media(max-width:1200px){
            
            padding: 0;
        }
        img{
            margin:1rem 0 0 20rem;
            
        }
    }

    .RightText{
        display: flex;
        flex-direction: column;
        margin:0 ;
        font-size: 14px;
       
        box-sizing: border-box;
        @media(max-width:1200px){
         
        }

        p{
           letter-spacing: 1px;
           white-space:1px;
           padding: 2px;
           justify-content: center;
           margin:0 0 0 16rem;
           
           @media(max-width: 1200px){
            padding:10px;
            margin:0 0 0 6rem;
           }
        }


        a{
            text-decoration: underline;
            color:black;
            margin:auto;

           
        }

        a:hover{
            color:gray;
        }

       .AdobeLogo{
            padding: 0;
            justify-content: flex-start;
            justify-items: flex-start;
            align-content: flex-start;
            align-items: flex-start;
            box-sizing: border-box;
            height: 50%;
            width:50%;
           @media (max-width:1200px) {
            padding: 1px;
            
                 }    
           img{
            width:80%;
            padding: 1px;
            justify-content: flex-end;
            margin: 0 0 0 15.5rem;
            
            
            
            @media(max-width:1200px){
              margin:0 0 0 10rem;
            }

            }
        }
        


    }

    .ContactUs{
        display: flex;
        flex-direction: column;
        justify-items: flex-end;
        justify-content: flex-end;
       
        @media (max-width:1200px){
            margin-top:auto;
        }
        
        
        a{
            color:black;
            margin:8rem 0 1.8rem 2rem;
            padding:0 10px;
            align-self: flex-end;
            align-items: flex-end;
            @media (max-width:1200px){
                margin-top:auto;
            }
            
        }
        form{ 
            
            display: flex;
            margin:0;
            justify-items: flex-end;
            justify-content: flex-end;
           
            input{
                
                margin:auto 4px;
                border-radius: 20px;
                border:1px solid lightgray;
                
                &::placeholder{
                    padding: 0.2rem;
                }
                
                &:focus{
                    outline: none;
                }

            }
            button{
               
                background-color:#61bafa;
                border-radius:20px;
                border: none;
                padding:1px 3px;
                color:black;   
                margin:  auto 0.2rem;
                font-weight:400;
                text-transform: uppercase;

                &:hover{
                    background-color:black;
                    color:white;
                    cursor: pointer;
                }
            }
        }

       
        
    }

}

